$(".burger").click(function () {
    $(".header").toggleClass("_open")
    $("body").toggleClass("fixed-body")
    $(window).scrollTop(0);
})

$(".nav-item__show").click(function () {
    if(window.innerWidth>992){
        return
    }

    $(this).parents(".nav-item").toggleClass("_open")
    $(this).siblings(".nav-item__hidden").slideToggle()
})

$(".header-catalog-item__show").click(function () {
    if(window.innerWidth>992){
        return
    }

    $(this).parents(".header-catalog-item").toggleClass("_open")
    $(this).siblings(".header-catalog-item__hidden").slideToggle()
})

$(".header-catalog-item").mouseover(function () {
    $(".header-catalog-item").removeClass("_hover")
    $(this).addClass("_hover")
})

$(".catalog-subitem").mouseover(function () {
    $(".catalog-subitem").removeClass("_hover")
    $(this).addClass("_hover")
})

$(".nav-item._catalog").mouseleave(function () {
    $(".catalog-subitem").removeClass("_hover")
    $(".header-catalog-item").removeClass("_hover")
})
