$(".m-select").each(function(){
    let parent = $(this)
    let toggle = $(this).find('.m-select__show')
    let current = $(this).find('.m-select__current')

    $(toggle).click(function(){
        $(parent).toggleClass("_open");
    })

    select()

    $(parent).find(".select-option input").change(function () {
        select()
    })

    function select(){
        let val = $(parent).find(".select-option input:checked ~ .select-option__label").html()
        let parentBlock = $(parent).parents("._tabs-parent")
        let tabId = $(parent).find(".select-option input:checked").attr("data-tab")


        $(parentBlock).find(".tab-content").removeClass("_active")
        $(parentBlock).find(`.${tabId}`).addClass("_active")
        $(current).html(val)
        $(parent).removeClass("_open");
    }

    $(document).mouseup( function(e){
        let div = $(parent);
        if ( !div.is(e.target)
            && div.has(e.target).length === 0 ) {
            if($(div).hasClass("_open")){
                $(div).removeClass("_open")
            }
        }
    });
})
